/* Timmystroge Oyetola 
Portfolio project
-variables
-reset
-bgs
-nav
-nav-links 
-social icon container
-mobile menu
-top header
-about-me
-skills
-services
-contactme
-footer
-bootstrap classes
-media queries*/

/* !Variables */
:root {
  --main: #0a44b9;
  --text-color: #333;
  --white: #ffffff;
  --greyish: lightgray;
  --black: #000;
}

/*! RESET START */
* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background: var(--white);
  font-family: calibri;
}

a:link,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

/*! RESET ENDS */

/* ! bgs */
.about-bg,
.service-bg {
  background: #f1f6ff;
}
.footer-bg {
  background-image: linear-gradient(to bottom right, #eadcd9, #d1dbe5, #eee7cb);
}
/* ! bgs ends */
.nav {
  background: rgba(255, 255, 255, 0.873);
  position: fixed;
  top: 0;
  width: 100%;
  box-shadow: 0px 0px 5px var(--text-color);
  backdrop-filter: blur(5px);
  z-index: 99;
}
.navbar-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: transparent;
}
.site-logo {
  font-size: 26px;
  font-weight: bold;
  color: var(--text-color);
}
.site-logo span {
  color: var(--main);
}
.navbar-wrapper {
  width: 55%;
  border-right: 1px solid rgba(173, 173, 173, 0.266);
}
.navbar-wrapper ul li a {
  color: var(--text-color);
}
.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px 15px 5px;
}
/* !navLink */

.navlink-wrapper ul {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navlink-wrapper ul li {
  margin: 0 10px;
  font-weight: bold;
  font-size: 15px;
}

/* !social icon container */
.social-icon-container {
  width: 45%;
  /* background: #f1f6ff; */
  background-image: linear-gradient(to bottom right, #eadcd9, #d1dbe5, #eee7cb);
}
.social-wrapper,
.description-icon,
.about-me-icons {
  background: transparent;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.social-wrapper {
  justify-content: flex-end;
  padding: 13px 0px 13px;
}
.description-icon {
  justify-content: flex-start;
  padding: 13px 0px 13px;
}
.social-wrapper span {
  margin: 2.5px 10px;
}
.social-wrapper span img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 5px;
}
/*! mobile menu  */
.Hamburger,
.back-cover {
  display: none;
}
.drop-down {
  display: none;
  z-index: 99999999999;
  max-width: 85%;
  height: 100vh;
  background: var(--main);
  padding: 20px 0;
}
.drop-ul li {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 5px;
  margin: 10px 0;
  border-radius: 4px;
}
.drop-ul li a {
  color: var(--white);
  font-size: 18px;
}
/* !Top-header  */
.top-header {
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: row;
}
.profile-text {
  width: 55%;
  margin-top: 3.5rem;
  border-left: 1px solid rgba(173, 173, 173, 0.086);
  padding: 15px 20px 15px 5px;
}
.profile-img {
  width: 45%;
  margin-top: 4rem;
  border-left: 1px solid rgba(173, 173, 173, 0.266);
  border-right: 1px solid rgba(173, 173, 173, 0.266);
  /* padding: 15px 0; */
}
.description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 8rem;
}
.description h5,
.description h3,
.description .description-icon {
  margin: 10px 0;
}
.description h5 {
  color: var(--main);
  font-size: 16px;
  font-weight: 500;
}
.description h3 {
  color: var(--main);
  font-size: 20px;
  font-weight: bold;
}
.description h1 {
  color: var(--black);
  font-size: 40px;
  font-weight: bold;
  margin: 12px 0;
}
.description p {
  color: var(--text-color);
  font-size: 16px;
  margin: 10px 0 15px;
  max-width: 80%;
}
.description-icon {
  position: relative;
  top: -14px;
}
.description-icon span {
  margin: 0 10px 2.5px 0;
}
.description-icon span img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 5px;
}
.description-icon span:last-child img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 6.5px 5.5px;
}

/*! profile img    */
.profile-img {
  /* background: #bdd4ff; */
  background-image: linear-gradient(to bottom right, #eadcd9, #d1dbe5, #eee7cb);
  position: relative;
}
.profileImg {
  width: 60%;
  height: 65%;
  border-radius: 10px;
  transform: rotate(6deg);
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.profileImg img {
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
}

/*! about-me */
.about-me {
  background: #f1f6ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 70px 0;
}
.profilepic-wrapper {
  display: none;
}
.profilepic-wrapper img {
  width: 100%;
}
.about-details h2 {
  font-size: 28px;
}
.about-details p {
  margin: 10px auto 15px;
  width: 40%;
  color: var(--text-color);
}
.about-me-icons span {
  margin: 2.5px 10px;
}
.about-me-icons span img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 5px;
}
.about-me-icons span:last-child img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 6px 5px;
}
/*! Skills */
.skills {
  padding: 70px 0px 60px;
  width: 100%;
}
.skills h2 {
  color: var(black);
}
.skills p {
  color: var(--text-main);
}
.skill-wrap {
  margin: 1.6rem 3rem 0;
  display: flex;
  justify-content: space-evenly;
}
.skill-wrap:last-child {
  margin: 0.5rem 3rem 0;
}
.skill-item {
  width: 500px;
  background: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  margin: 10px;
  box-shadow: 0 0 5px inset lightgray;
}
.skill-img-wrap {
  width: 35px;
  display: flex;
}
.skill-img {
  width: 100%;
}
.skill-progress {
  width: 100%;
  padding: 5px;
}
.skill-progress p {
  font-weight: 600;
  text-transform: capitalize;
}
.skill-progress .progress-bar1 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar1:before {
  content: "";
  width: 95%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}
.skill-progress .progress-bar2 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar2:before {
  content: "";
  width: 90%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}
.skill-progress .progress-bar3 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar3:before {
  content: "";
  width: 50%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}
.skill-progress .progress-bar4 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar4:before {
  content: "";
  width: 80%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}
.skill-progress .progress-bar5 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar5:before {
  content: "";
  width: 75%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}
.skill-progress .progress-bar6 {
  width: 100%;
  background-color: #cbd8ff;
  height: 5px;
  position: relative;
  border-radius: 5px;
  margin: 5px 0;
}
.skill-progress .progress-bar6:before {
  content: "";
  width: 95%;
  background-color: var(--main);
  height: 5px;
  position: absolute;
  border-radius: 5px;
}

/*! services */
.servicesrender {
  background: #f1f6ff;
  padding: 60px 10px;
}
.service-wrap {
  margin: 2rem 3rem 0 3rem;
  display: flex;
  justify-content: space-evenly;
}
.service-item {
  margin: 5px;
  padding: 10px;
}
.service-img-wrap {
  width: 34px;
}
.service-img {
  width: 100%;
}
.services-details {
  width: 100%;
}
.s_text {
  color: var(--text-color);
  font-size: 15px;
  width: 40%;
  margin: 0 auto;
}
.services-details h4 {
  font-weight: bold;
  margin: 7px 0;
}
.services-details p {
  margin-bottom: 4px;
  color: #4d4d4d;
  font-size: 14px;
}

/*! contact me  */
.contact-wrapper {
  display: flex;
}
.get_in_touch {
  width: 60%;
  padding: 60px 20px 60px 65px;
}
.contact_form {
  padding: 60px 60px 60px 60px;
  width: 40%;
  background: #0a44b9;
}

.get_in_touch .get-text h2 {
  font-size: 32px;
  margin-bottom: 10px;
}
.get_in_touch .get-text p {
  font-size: 14px;
  color: var(--text-color);
  margin-bottom: 10px;
  width: 50%;
}
.contact-icon-wrap {
  display: flex;
  align-items: center;
}
.contact-icon-wrap span {
  margin: 10px 10px 10px 0;
}
.mail-wrap {
  width: 28px;
  margin-right: 10px;
}
.mail-call-icon {
  width: 100%;
}
.mail-text h3 {
  font-size: 13px;
}
.mail-text p {
  font-size: 12px;
  color: var(--text-color);
}

.contact_form .contact-text h3 {
  color: rgba(220, 220, 220, 0.899);
  margin-bottom: 3px;
  font-size: 16px;
}

.contact_form .contact-text h2 {
  color: var(--white);
  margin-bottom: 5px;
  margin-bottom: 10px;
}
.row form {
  display: flex;
  flex-direction: column;
  width: 80%;
}
.row form .form-control,
.row form .form-control {
  border: none;
  background: transparent;
  border-bottom: 1px solid lightgray;
  outline: none;
  margin: 10px 0;
  padding: 10px 5px 10px 0;
  color: var(--greyish);
}
.row form .form-control::placeholder,
.row form .form-control::placeholder {
  color: var(--greyish);
}

.submit-wrap {
  margin-top: 15px;
}
.submit {
  padding: 10px 30px;
  border-radius: 5px;
  border: none;
  color: var(--main);
  background: var(--white);
  cursor: pointer;
}
/*! footer  */
.footer {
  padding: 50px 60px;
  background-image: linear-gradient(to bottom right, #eadcd9, #d1dbe5, #eee7cb);
}
.footer-wrap{
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-wrap h2 {
  padding: 0 30px;
  font-size: 28px;
}
.footer-line {
  width: 40%;
  height: 1px;
  background: var(--main);
  margin: 0 20px;
}
.footer-icon {
  position: relative;
  margin: 20px 0;
  text-align: center;
}
.footer-icon span {
  margin: 0 10px 2.5px 0;
}
.footer-icon span img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 5px;
}
.footer-icon span:last-child img {
  width: 15px;
  border: 1px solid #0a44b9;
  border-radius: 50%;
  padding: 6.5px 5.5px;
}
.copywrite {
  font-weight: bold;
  font-size: 12px;
}

/*! bootstrap class */
.container {
  max-width: 93%;
  margin: 0 auto;
}
.text-center {
  text-align: center;
}
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}
.flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/*! Media Queries   */
@media (max-width: 1023px) {
  .navbar {
    padding: 15px 0px 15px 5px;
  }
  .description {
    margin-top: 5rem;
  }

  /*! profile img    */
  .profileImg {
    width: 70%;
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .description {
    margin-top: 9rem;
  }
  .profileImg img {
    width: 100%;
    object-fit: cover;
  }
  .profile-text {
    padding: 15px 0px 15px 5px;
  }
  /*! contact me  */
  .get_in_touch .get-text p {
    width: 80%;
  }
  .row form {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .contact_form {
    padding: 60px 40px 60px 40px;
    width: 40%;
    background: #0a44b9;
  }
}
@media (max-width: 768px) {
  .site-logo {
    font-size: 26px;
  }
  .container {
    max-width: 93%;
  }
  .navlink-wrapper {
    display: none;
  }
  .navbar-wrapper {
    width: 100%;
    border-right: none;
  }
  .social-icon-container {
    display: none;
  }
  /*! mobile menu  */
  .Hamburger {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 5px;
    right: 6px;
  }
  .back-cover {
    display: block;
    background-color: #f1f6ff;
    position: relative;
    width: 35px;
    height: 28px;
    z-index: -1;
    border-radius: 4px;
    box-shadow: 0px 0px 1px inset var(--text-color);
  }

  .Hamburger span {
    width: 22px;
    height: 2px;
    margin: 2.2px 0;
    background: var(--text-color);
    border-radius: 90px;
  }
  /*! top-header   */
  .top-header {
    flex-direction: column;
  }
  .profile-text {
    width: 100%;
    border-left: none;
  }
  .description {
    margin-top: 5rem;
  }
  .profile-img {
    display: none;
  }
  .description p {
    font-size: 16px;
    margin: 0px 0 12px;
    max-width: 90%;
  }

  /*! about-me */
  .profilepic-wrapper {
    width: 11rem;
    margin: 0 0 15px;
    display: block;
  }
  .profilepic-wrapper img {
    width: 100%;
  }
  .about-details h2 {
    font-size: 25px;
  }
  .about-details p {
    margin: 5px auto 15px;
    width: 60%;
  }
  .about-me-icons span {
    margin: 2.5px 10px;
  }

  /*! skills */
  .skill-wrap {
    margin: 1.6rem 0 0;
    flex-wrap: wrap;
  }
  .skill-wrap:last-child {
    margin: 10px 0 0;
  }
  .skill-item {
    margin: 5px 0;
  }

  /*! services */
  .service-wrap {
    margin: 2rem 0;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .service-item {
    margin: 10px;
    padding: 10px;
  }
  .s_text {
    width: 50%;
  }
  /*! contact me  */
  .contact-wrapper {
    flex-direction: column;
    width: 100%;
  }

  .get_in_touch {
    width: 100%;
    padding: 30px;
  }
  .contact_form {
    padding: 30px;
    width: 100%;
  }
  .row form {
    width: 80%;
  }
  /* ! footer */
  .footer-wrap h2 {
    padding: 0 30px;
    font-size: 28px;
  }
  .footer-line {
   display: none;
  }
}
@media (max-width: 479px) {
  .container {
    max-width: 92%;
  }
  .description {
    margin-top: 5rem;
  }
  .description h5,
  .description h3,
  .description .description-icon {
    margin: 8px 0;
  }
  .description h5 {
    font-size: 16px;
  }
  .description h3 {
    font-size: 18px;
  }
  .description h1 {
    font-size: 34px;
    margin: 8px 0;
  }
  .description p {
    font-size: 16px;
    margin: 0px 0 12px;
    max-width: 100%;
  }
  /*! about-me */
  .profilepic-wrapper {
    width: 11rem;
    margin: 0 0 15px;
    display: block;
  }
  .profilepic-wrapper img {
    width: 100%;
  }
  .about-details h2 {
    font-size: 22px;
  }
  .about-details p {
    margin: 5px auto 15px;
    width: 80%;
  }

  /*! services */
  .service-item {
    margin: 0px;
    padding: 15px 0;
  }
  .s_text {
    width: 100%;
  }
  /*! service */
  .get_in_touch {
    padding: 30px 10px;
  }
  .contact_form {
    padding: 30px 10px;
  }
}
@media (max-width: 425px) {
  .description {
    margin-top: 6rem;
  }
}
@media (max-width: 375px) {
  .description h5,
  .description h3,
  .description .description-icon {
    margin: 5px 0;
  }
  .description h5 {
    font-size: 16px;
  }
  .description h3 {
    font-size: 18px;
  }
  .description h1 {
    font-size: 30px;
    margin: 8px 0;
  }
  .description p {
    font-size: 16px;
    margin: 0px 0 12px;
    max-width: 100%;
  }
}
@media (max-width: 320px) {
  .description {
    margin-top: 3rem;
  }
}
@media (max-width: 280px) {
}
